<template>
  <div class="card--number">
    <div class="card--number__item">
      {{statistic.item | toUpperCase}}
    </div>
    <div class="card--number__number">
      {{statistic.number | toLocaleString}}
    </div>
  </div>
</template>

<script>
export default {
  name: 'numberCard',
  props: {
    statisticFromParents: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      statistic: this.statisticFromParents
    }
  },
  filters: {
    toUpperCase (value) {
      return value.toUpperCase()
    },
    toLocaleString (value) {
      return value.toLocaleString()
    }
  }
}
</script>

<style lang="scss" scoped>
.card--number {
  display: flex;
  flex-direction: column-reverse;
  text-align: center;

  &__number {
    margin: 2rem auto .25rem;
    font-size: clamp(24px, 3vw, 36px);
  }
}

@media screen and (min-width: 768px) {
  .card--number {
    margin-bottom: 2rem;
  }
}
</style>