<template>
  <div :class="['card--image-bg-with-text', { 'pure-article': !article.image }]">
    <img v-if="article.image" class="card--image-bg-with-text__background"
    :src="article.image">
    <div class="card--image-bg-with-text__category">
      {{ article.category }}
    </div>
    <div class="card--image-bg-with-text__title">
      {{article.title}}
    </div>
    <div class="card--image-bg-with-text__date">
      {{article.date}}
    </div>
  </div>
</template>

<script>
export default {
  name: "imageBackgroundCard",
  props: {
    articleFromParents: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      article: this.articleFromParents,
      backgroundUrl: `background-image: url(${this.articleFromParents.image});`
    };
  },
};
</script>

<style lang="scss" scoped>
.card--image-bg-with-text {
  position: relative;
  width: 100%;
  height: clamp(180px, 50vw, 600px);
  margin: 0 auto;
  padding: 1rem;
  background-color: var(--light);
  transition: .4s;

  &+.card--image-bg-with-text {
    margin-top: 1rem;
  }

  &:hover {
    transform: translateY(-4px);
    box-shadow: 0 1px 8px var(--dark);
  }

  &__background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__category,
  &__title,
  &__date {
    position: relative;
    z-index: 1;
  }

  &__title {
    margin: .5rem 0;
    font-size: clamp(18px, 2vw, 26px);
  }

  &__date {
    font-size: 12px;
  }
}

.pure-article {
  height: 180px;
}

@media screen and (min-width: 768px) {
  .card--image-bg-with-text {
    height: auto;

    &+.card--image-bg-with-text {
    margin-top: unset;
  }
  }
}
</style>>